@import url(http://fonts.googleapis.com/css?family=Open+Sans:300italic,400italic,600italic,700italic,800italic,400,600,700,300,800);
@import './components/_fonts.scss';
@import './components/_media-queries.scss';
@import './components/_library.scss';

$wikaOrange: #ef8122;
$wikaDarkOrange: #f57e20;
$wikaBlack: #001429;
$wikaDarkBlue: #171a3f;
$wikaGrey: #9b9b9b;


body {
  background: #fff;
  font-family: 'Helvetica';
  color: #4e4e4e;
  margin: 0;
}

h1, h2, h3, h4, h5 {
  font-family: 'Helvetica';
  color: $wikaDarkBlue;
  &.bold, .bold {
    font-family: 'Helvetica-Bold';
  }
}

p {
  font-family: 'MyriadPro';
  color: $wikaDarkBlue;
}

a {
  font-family: 'MyriadPro';
}

.section {
  &-container {
    margin-top: 2rem;
  }
  &-title {
    font-family: 'Helvetica';
    font-size: 40px;
    margin-bottom: 1rem;
    &.bold, .bold {
      font-family: 'Helvetica-Bold';
    }
  }
}


/* #Footer Section */
#FooterMenu {
  background-color: $wikaGrey;
  padding: 2rem 0;
  ul {
    list-style-type: none;
    li {
      padding: 4px 0;
    }
  }
  a {
    text-decoration: none;
  }
  a, p {
    color: #fff;
  }
}

/* Header Menu Section */
.header-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 9999;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 1);
  box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
  @include breakpoint(sm) {
    display: block;
    background-color: rgba(255, 255, 255, 1);
    padding: 0;
  }

  &.on-scroll {
    background-color: rgba(255, 255, 255, 0.8);
    @include breakpoint(sm) {
      background-color: rgba(255, 255, 255, 1);
    }
  }

  .mobile-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include breakpoint(sm) {
      padding: 1rem;
    }
  }

  .menu-logo {
    display: inline-block;
    max-width: 240px;
    height: auto;
    @include breakpoint(sm) {
      max-width: 140px;
    }
  }
  
  .burger-menu {
    display: none;
    cursor: pointer;
    @include breakpoint(sm) {
      display: inline-block;
    }
    font-size: 24px;
    color: $wikaDarkBlue;
  }
}

.menu-list-container {
  display: flex;
  @include breakpoint(sm) {
    display: block;
    overflow-y: hidden;
    transition: max-height .6s, padding .6s;
    max-height: 0;
    padding: 0 .5rem;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 6px 12px -2px, rgba(0, 0, 0, 0.3) 0px 3px 7px -3px;
  }
  &.show {
    display: block;
    padding: 0 .5rem;
    padding-bottom: 1rem;
    @include breakpoint(sm) {
      max-height: 80vh;
      overflow-y: auto;
    }
  }

  .menu-level {
    &-1 {
      padding: 1rem;
      @include breakpoint(sm) {
        padding: 0.4rem 0.6rem;
        border-bottom: 1px solid #f0f0f0;
      }
      &:hover {
        .menu-level-2 {
          display: block;
          @include breakpoint(sm) {
            display: none;
          }
        }
      }
      a {
        display: block;
        text-decoration: none;
        color: $wikaBlack;
        span {
          display: inline-block;
        }

        &.active, &:hover {
          color: $wikaOrange;
          transition: ease 0.3s ;
        }
      }
      .menu-icon {
        font-size: .8rem;
        margin-left: 2px;
        display: none;
        &.show {
          display: inline-block;
        }
      }
    }
    &-2 {
      position: absolute;
      z-index: 1000;
      display: none;
      min-width: 10rem;
      padding: 0.5rem 0;
      margin: 0;
      text-align: left;
      list-style: none;
      background-color: #fff;
      background-clip: padding-box;
      border-radius: 0.375rem;
      box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
      margin-top: 0.5rem;
      @include breakpoint(sm) {
        display: none;
        position: static;
        box-shadow: none;
        margin-top: 0;
        padding: .5rem 0;
        overflow-y: auto;
      }

      li {
        padding: .5rem 1rem;
        a {
          display: flex;
          align-items: center;
          justify-content: space-between;
          span {
            display: block;
            flex: 1;
          }
          .menu-icon {
            padding-left: 1rem;
          }
        }
      }
    }
  }
}

/* #Banner Section */
.banner {
  &-container {
    &:hover {
      .slick-arrow {
        opacity: 1;
      }
    }
    @include breakpoint(sm) {
      margin-top: 70px;
    }
  }
  &-items {
    position: relative;
    height: 800px;
    @include breakpoint(sm) {
      height: 70vh;
    }
    &__images {
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      height: 100%;
      &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.25);
      }
    }
    &__text-container {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      display: flex;
      align-items: center;
      padding-left: 5rem;
      padding-right: 5rem;
      max-width: 60%;
      @include breakpoint(sm) {
        padding-left: 1rem;
        padding-right: 1rem;
        max-width: 100%;
        align-items: flex-end;
        margin-bottom: 2rem;
      }
    }
    &__title {
      font-family: 'Helvetica-Bold';
      display: block;
      color: #fff;
      font-size: 3rem;
      line-height: 3rem;
      margin-bottom: 3rem;
      @include breakpoint(sm) {
        font-size: 2rem;
        line-height: 2rem;
        margin-bottom: 2rem;
      }
    }
    &__desc {
      font-family: 'Helvetica';
      color: #fff;
      font-size: 1.6rem;
      @include breakpoint(sm) {
        font-size: 1.2rem;
      }
    }
  }
}

/* #About Section */
.about {
  &-desc {
    font-size: 18px;
    text-align: justify;
    font-weight: 600;
    .highlight {
      background-color: $wikaDarkOrange;
      padding: 2px 8px;
      border-radius: 100px;
      font-style: italic;
      font-weight: bold;
    }
  }
  &-image-container {
    margin-right: 2rem;
  }
  &-image {
    width: 479px;
    height: auto;
    @include breakpoint(sm) {
      width: 200px;
      margin: auto;
      margin-bottom: 1rem;
    }
  }
}

/* #OurProduct Section */
.product-images {
  filter: grayscale(1);
  &:hover {
    filter: grayscale(0);
    transition: ease-in .2s;
  }
}

/* #MediaHighlight Section */
#Media {
  .slick-arrow {
    top: 30%;
  }
}

.media {
  &-images {
    margin-bottom: 1rem;
    width: 525px;
    height: auto;
  }
  &-desc {
    text-align: justify;
  }
}


/* Footer */
.copyright {
  background-color: #f8991d;
  padding: 30px 0;
  p {
    margin-bottom: 0;
    color: #fff;
  }
}